@tailwind base;
html {
  @apply bg-gray-50 min-h-screen flex flex-col;
}

body {
  @apply font-sans flex-grow flex flex-col;
}

#app {
  @apply flex-grow flex flex-col;
}
#app > div {
  @apply flex-grow flex flex-col;
}
#app #main {
  @apply flex-grow;
}

[x-cloak] {
  @apply hidden;
}

button,
a {
  @apply transition duration-200;
}

.brand {
  @apply text-3xl font-medium text-indigo-500;
}

.about-container .about-item:nth-child(1) {
  @apply mt-0 md:mt-12 lg:mt-16;
}
.about-container .about-item:nth-child(even) .content-container {
  @apply lg:col-start-2;
}
.about-container .about-item:nth-child(even) .img-item {
  @apply lg:col-start-1;
}
.about-container .about-item:nth-child(even) .img-container {
  @apply -ml-48 md:-ml-16 mr-0 md:mr-0 xl:mx-0;
}

.contact-item a {
  @apply hover:underline;
}

.banner-text a {
  @apply underline font-bold;
}

@font-face {
  font-family: "GT America";
  src: url("../fonts/GT America Regular.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "GT America";
  src: url("../fonts/GT-America-Standard-Bold.otf") format("opentype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Whyte";
  src: local("Whyte Medium"), local("Whyte-Medium"), url("../fonts/Whyte-Medium-New.woff2") format("woff2"), url("../fonts/Whyte-Medium-New.woff") format("woff"), url("../fonts/Whyte-Medium-New.otf") format("opentype");
  font-weight: 500;
  font-style: normal;
  vertical-align: middle;
}
.gfield {
  @apply mb-4 md:w-full;
}
.gfield input,
.gfield select,
.gfield textarea {
  @apply w-full bg-white focus:outline-none text-black shadow-sm border-gray-300 py-3 placeholder-gray-500 rounded-md;
}
.gfield.gfield_error input,
.gfield.gfield_error select,
.gfield.gfield_error textarea {
  @apply ring-2 ring-red-500;
}
.gfield textarea {
  @apply h-40;
}
.gfield .gfield_label {
  @apply hidden;
}

.gform_confirmation_message {
  @apply text-center;
}

.hidden_label label {
  @apply hidden;
}

.gfield_visibility_hidden {
  @apply hidden;
}

.validation_message {
  @apply hidden;
}

.validation_error,
.gform_submission_error {
  @apply text-red-500 text-base px-0 mb-4 md:mb-4 md:w-4/5 md:mx-auto md:text-center !important;
}

.gform_footer {
  @apply flex justify-center mt-6;
}
.gform_footer .gform_button {
  @apply bg-transparent text-c-blue-100 border-2 border-c-blue-100 rounded-full w-auto font-bold px-12 py-2 hover:bg-c-blue-100 hover:text-c-blue-300 cursor-pointer;
}

.gform_validation_container {
  @apply hidden;
}

.gform_ajax_spinner {
  @apply hidden;
}

.contact-form .gfield {
  @apply mb-4 lg:mb-6;
}
.contact-form .gform_footer {
  @apply flex justify-center;
}
.contact-form .gform_footer .gform_button {
  @apply bg-green-800 text-white rounded-full py-3 border-0 w-auto px-10 hover:bg-green-600;
}

@tailwind components;
@tailwind utilities;